import Contructor from './class';
import messager from './messager';
import 'style-loader!css-loader!app/app.pass';
import Query from './query';

let page = 'pages/index/index';

messager
  .recevie('UPDATE', (data) => {
    let instance = Contructor.instance(data.id);
    instance.$render = () => data.vnode;
    instance.$forceUpdate();
  }).recevie('MOUNT', (data) => {
    let instance = Contructor.instance(data.id);
    instance.$render = () => data.vnode;
    instance.$invokeMount(data.id == page ? document.getElementById('app') : void 0);
  }).send('PAGE_LOADED', page, (data) => {
    new Contructor(data, {
      componentId: page
    });
  });

messager.recevie('QUERY', (data, callback) => {
  callback(Query.exec(Contructor.instance(data.id), data));
});